<template>
	<div id="shopSeting" class="warp">
		<!--头部导航 start-->
		<nav class="nvs">
			<el-breadcrumb separator=">">
				<el-breadcrumb-item :to="{ path: '/shop' }">店铺管理</el-breadcrumb-item>
				<el-breadcrumb-item :to="{path:'/shop/shopinfo'}">店铺信息</el-breadcrumb-item>
				<el-breadcrumb-item>编辑店铺设置</el-breadcrumb-item>
			</el-breadcrumb>
		</nav>
		<!--头部导航 end-->
		<div class="title">
			编辑店铺设置
		</div>
		<!--编辑店铺设置 start-->
		<section class="shopSeting">
			<div class="setInfo">
				<el-form label-width='110px'>
					<el-form-item class='main ' id='top' label="扫码点餐系统">
						<button type="button" class="start" v-for="(item,index)  in buttons" @click='satrt(index)' :class="{'active':startsystem==index}">{{item}}</button>
						<p class="injection">注：选暂停时，客人手机上会显示为系统升级维护中</p>
					</el-form-item>
					<el-form-item class='main' id='top' label='启用支付方式'>
						<el-checkbox-group v-model="pay">
							<el-checkbox-button v-for="payment in Payments" :label="payment.idx" :key="payment.text">
								{{payment.text}}
							</el-checkbox-button>
							<p class="injection">注：若要启用“支付宝支付”、“微信支付”、请先进入“收银设置”进行相应的配置</p>
						</el-checkbox-group>
					</el-form-item>
					<el-form-item class='main' label='订单付款时间'>
						<el-checkbox-group v-model="timePay">
							<el-checkbox-button v-for="paytimed in Paytime" :label="paytimed.idx " :key="paytimed.text ">
								{{paytimed.text}}
							</el-checkbox-button>
						</el-checkbox-group>
					</el-form-item>
					<el-form-item class='main' label='销售方式'>
						<el-checkbox-group v-model="dinner">
							<el-checkbox-button v-for="sells in sell" :label="sells.idx " :key="sells.text ">{{sells.text}}</el-checkbox-button>
						</el-checkbox-group>
					</el-form-item>
					<el-form-item class='main' label='店铺标签'>
						<div v-model='shoplabel'>
							<span class="label" v-for='item in shoplabel'>{{item}}</span>
							<span class="add" @click="addlabel">编辑</span>
						</div>
					</el-form-item>
					<el-form-item class='businessTime' label="营业时间">
						<el-time-picker v-model="value2" @change="bustimeStartChange">
						</el-time-picker>
						<el-time-picker v-model="value3" @change="bustimeEndChange">
						</el-time-picker>
					</el-form-item>
					<el-form-item class='dateSet' label="餐时设置">
						<el-checkbox-group v-model="type">
							<el-checkbox :label="Dinnertime.MORN" name="type">{{Dinnertime.toString(Dinnertime.MORN)}}</el-checkbox>
							<el-time-picker v-model="value4" @change="dinnemoStartChange" :picker-options="{
						      selectableRange: '00:00:00 - 23:59:59'
						   }">
							</el-time-picker>
							<el-time-picker v-model="value5" @change="dinnemoEndChange" :picker-options="{
						      selectableRange: '07:00:00 - 23:59:59'
						   }">
							</el-time-picker>
							<el-checkbox :label="Dinnertime.NOON" name="type">{{Dinnertime.toString(Dinnertime.NOON)}}</el-checkbox>
							<el-time-picker v-model="value6" @change="dinnenoStartChange" :picker-options="{
						      selectableRange: '10:00:00 - 23:59:59'
						   }">
							</el-time-picker>
							<el-time-picker v-model="value7" @change="dinnenoEndChange" :picker-options="{
						      selectableRange: '11:00:00 - 23:59:59'
						   }">
							</el-time-picker>
							<el-checkbox :label="Dinnertime.EVEN" name="type">{{Dinnertime.toString(Dinnertime.EVEN)}}</el-checkbox>
							<el-time-picker v-model="value8" @change="dinneevStartChange" :picker-options="{
						      selectableRange: '16:00:00 - 23:59:59'
						   }">
							</el-time-picker>
							<el-time-picker v-model="value9" @change="dinneevEndChange" :picker-options="{
						      selectableRange: '17:00:00 - 21:59:59'
						   }">
							</el-time-picker>
							<el-checkbox :label="Dinnertime.NIGHT" name="type">{{Dinnertime.toString(Dinnertime.NIGHT)}}</el-checkbox>
							<el-time-picker v-model="value10" :picker-options="{
						      selectableRange: '22:00:00 - 23:59:59'
						   }">
							</el-time-picker>
							<el-time-picker v-model="value11" :picker-options="{
						      selectableRange: '00:00:00 - 05:59:59'
						   }">
							</el-time-picker>
						</el-checkbox-group>
					</el-form-item>
					<el-form-item class='invoice nomain clearfix' label="发票">
						<button type="button" class='noInvoice left' @click='shows' :class="{'active':show===Invoce.NO}">{{Invoce.toString(Invoce.NO)}}</button>
						<span class="line"></span>
						<button type="button" class="paperInvoice" @click='showInvos' :class="{'selectd':showinvos===Invoce.PAPERINVOICE}">{{Invoce.toString(Invoce.PAPERINVOICE)}}</button>
						<button type="button" class="electronicInvoice" @click='addCls' :class="{'selectd':showinvs===Invoce.ELECTRONICINVOICE}">{{Invoce.toString(Invoce.ELECTRONICINVOICE)}}</button>
					</el-form-item>
					<el-form-item class='paperTickets' label="纸票" v-show='showvos'>
						<el-checkbox-group v-model="inType">
							<el-checkbox-button v-for="papers in paper" :label="papers.idx" :key="papers.text">{{papers.text}}</el-checkbox-button>
						</el-checkbox-group>

					</el-form-item>
					<el-form-item label="发票备注">
						<el-input type='textarea' :maxlength=60 v-model='remark' placeholder='备注文字总数不得超过120字'></el-input>
					</el-form-item>
					<el-form-item class='clearfix' label="店铺图片">
						<div class="shopinfo left" v-if="shoppic.length==0">
							<img src="../../img/res.png" alt="店铺图片" />
						</div>
						<div class="shopinfo left" ref='info' v-for="(item,index) in shoppic" v-else>
							<img :src="imgbase_url+'/img_get.php?img=1&height=100&width=100&imgname='+item" alt="店铺图片" />
							<div class="bg" ref='bg'></div>
							<i class="el-icon-delete2" @click="delt(index)"></i>
							<i class="el-icon-view" @click="clickImg(index)"></i>
						</div>
						<img-upload @send-image='getShopphoto' :FreedomCrop="true" :ImageSize='imgSize'></img-upload>
						<div class="text-warp">
							<p class="text top">注：最多可上传五张图片，支持JPG、JPEG、PNG文件格式，大小不得大于1M</p>
						</div>

					</el-form-item>
					<div class="bottom">
						<button type="button" class="blue" @click="save">保存</button>
						<button type="button" class="default" @click="goBack">取消</button>
					</div>
				</el-form>
			</div>
		</section>
		<!--编辑店铺设置 end-->
		<!-- 餐厅标签编辑弹窗 -->
		<tg :dialogVisible="dialogVisible" :selectArr="shoplabel" :tagObj="labelObj" @on-close="hideDialog" @on-change='getLabel'></tg>
		<!-- 餐厅标签编辑弹窗 -->
		<!--图片放大弹窗-->
		<big-img v-if="showImg" @clickit="viewImg" :imgSrc="imgSrc"></big-img>
		<!--图片放大弹窗-->
		<!--保存成功失败 Tips start-->
		<div class="tips" v-if='showTips'>
			<span class="info">{{msg}}</span>
		</div>
		<!--保存成功失败 Tips end-->
	</div>
</template>

<script>
	import moment from "moment";
	import { shopSeting, getShopinfo } from '../../../api';
	import { Subtime, addZero, Tranobj, timeToArr } from '../../../index';
	import tg from '../../../../merchandise/components/tagEditor';
	import ImgUpload from '@/components/upload/SUpload';
	import BigImg from '../../../components/showBigImg';
	import { Util } from '@/config/util';
	import { Public } from '@/config/public';
	import { Invoce, Startsystem, Payway, PayTime, Saleway, Restauran, Invoice, Dinnertime } from '@/config/cfg';
	export default {
		components: {
			tg,
			ImgUpload,
			BigImg
		},
		data() {
			return {
				dialogVisible: false, //控制添加餐厅标签弹窗
				show: Invoce.YES, //控制是否提供发票
				showinvos: Invoce.NO, //控制显示纸票
				showvos: Invoce.SHOWPAPERINVOICE, //控制显示纸票下的子类(普通，专用)
				showinvs: Invoce.NO, //控制显示电子发票
				buttons: [Startsystem.toString(Startsystem.START), Startsystem.toString(Startsystem.PAUSE)], //显示(启用，暂停)
				/*支付方式*/
				pay: [],
				Payments: [{
						text: Payway.toString(Payway.CASHPAYMENT),
						idx: Payway.CASHPAYMENT
					},
					{
						text: Payway.toString(Payway.CARDPAYMENT),
						idx: Payway.CARDPAYMENT
					},
					{
						text: Payway.toString(Payway.ALIPAY),
						idx: Payway.ALIPAY
					},
					{
						text: Payway.toString(Payway.WECHATPAY),
						idx: Payway.WECHATPAY
					},
					{
						text: Payway.toString(Payway.IOUPAY),
						idx: Payway.IOUPAY
					}
				],
				/*支付方式*/

				/*付款时机*/
				timePay: [],
				Paytime: [{
						text: PayTime.toString(PayTime.BEFOREPAY),
						idx: PayTime.BEFOREPAY
					},
					{
						text: PayTime.toString(PayTime.AFTERPAY),
						idx: PayTime.AFTERPAY
					}
				],
				/*付款时机*/

				/*用餐方式*/
				dinner: [],
				sell: [{
						text: Saleway.toString(Saleway.SHOPEAT),
						idx: Saleway.SHOPEAT
					},
					{
						text: Saleway.toString(Saleway.PACKAGING),
						idx: Saleway.PACKAGING
					},
					{
						text: Saleway.toString(Saleway.TAEOUT),
						idx: Saleway.TAEOUT
					},
					{
						text: Saleway.toString(Saleway.PICKUP),
						idx: Saleway.PICKUP
					}
				],
				/*用餐方式*/

				/*餐厅标签*/
				shoplabel: [],
				/*餐厅标签*/
				/*发票分类*/
				inType: [],
				paper: [{
						text: Invoice.toString(Invoice.PLIANINVOICE),
						idx: Invoice.PLIANINVOICE
					},
					{
						text: Invoice.toString(Invoice.SPECIALINVOICE),
						idx: Invoice.SPECIALINVOICE
					}
				],
				/*发票分类*/
				startsystem: 0, //控制启用 还是暂停系统 ， 默认是启用。
				shopsystem: {
					img_list: [] //店铺图片
				},
				/*获取时间*/
				value2: new Date(2017, 10, 11, 0, 0, 0), //营业时间开始
				value3: new Date(2017, 10, 11, 23, 59, 59), //营业时间结束
				value4: '', //早市开始时间
				value5: '', //早市结束时间
				value6: '', //午市开始时间
				value7: '', //午市结束时间
				value8: '', //晚市开始时间
				value9: '', //晚市结束时间
				value10: '', //夜宵开始时间
				value11: '', //夜宵结束时间
				/*获取时间*/
				type: [], //选中的餐时保存数组  如：早市
				remark: '', //发票备注
				imgbase_url: imgbase_url, //图片路径
				opening_time: [], //餐时设置
				invoies: { //发票
					is_invoice: Invoce.YES, //支持与不支持发票
					invoice_type: [] //支持发票类型
				},
				labelObj: { //添加餐厅标签弹窗的配置
					name: '餐厅标签',
					value: 'shop_label',
					limitNum: 30,
					limitWord: 5,
					limitSelect: 5
				},
				shoppic: [], //保存店铺图片
				imgSize: 1024, //长传图片尺寸上限
				imgSrc: '', //图片地址
				showImg: false, //显示大图
				/*判断开始时间不能大于结束时间*/
				result: null,
				moresults: null,
				noresults: null,
				evresults: null,
				/*判断开始时间不能大于结束时间*/
				showTips:false, //控制显示Tips提示框
				msg:'' //提示框提示内容
			};
		},
		created() {
			
		},
		mounted(){
			this.getData(); //调用请求数据
		},
		computed: {
			Invoce() {
				return Invoce;
			},
			Startsystem() {
				return Startsystem;
			},
			Payway() {
				return Payway;
			},
			PayTime() {
				return PayTime;
			},
			Saleway() {
				return Saleway;
			},
			Restauran() {
				return Restauran;
			},
			Invoice() {
				return Invoice;
			},
			Dinnertime() {
				return Dinnertime;
			}
		},
		methods: {
			//图片
			getShopphoto(res) {
				if(this.shoppic.length >= 5) {
					this.showTips = true;
					this.msg = '最多只能上传5张图片';
					setTimeout(()=>{
						this.showTips = false;
					},1500);
					return;
				}
				this.shoppic.push(res);

			},
			delt(index) {
				this.shoppic.splice(index, 1)
			},
			//不提供发票点击事件
			shows() {
				this.show = Invoce.NO;
				this.showinvos = Invoce.NO;
				this.showinvs = Invoce.NO;
				this.showvos = Invoce.NO;
			},
			//系统按钮点击事件
			satrt(index) {
				this.startsystem = index;
			},
			//转换时间
			formatTime(t, n) {
				if(n) {
					return t.toString().split(' ')[4]
				}
				if(t) {
					let arr = t.toString().split(' ')[4].split(':');
					return {
						"hh": arr[0],
						"mm": arr[1],
						"ss": arr[2]
					}
				}
			},

			//保存数据
			save() {

				if(Util.isEmpty(this.value2) || Util.isEmpty(this.value3)) {
					this.showTips = true;
					this.msg = '请将营业时间填写完整';
					setTimeout(()=>{
						this.showTips = false;
					},1500);
					return;
				}
				if(this.type.length == 0) {
					this.showTips = true;
					this.msg = '餐时设置选择必须选一个';
					setTimeout(()=>{
						this.showTips = false;
					},1500);
					return;
				}
				let err = false;
				this.type.forEach(item => {
					if(item === Dinnertime.MORN) {
						if((Util.isEmpty(this.value4) || this.value4 == '') || (Util.isEmpty(this.value5) || this.value5 == '')) {
							this.showTips = true;
							this.msg = '早市餐时设置必须填写完整';
							setTimeout(()=>{
								this.showTips = false;
							},1500);
							return err = true;
						}
					}
					if(item === Dinnertime.NOON) {
						if((Util.isEmpty(this.value6) || this.value6 == '') || (Util.isEmpty(this.value7) || this.vlaue7 == '')) {
							this.showTips = true;
							this.msg = '午市餐时设置必须填写完整';
							setTimeout(()=>{
								this.showTips = false;
							},1500);
							return err = true;
						}
					}
					if(item === Dinnertime.EVEN) {
						if((Util.isEmpty(this.value8) || this.value8 == '') || (Util.isEmpty(this.value9) || this.value9 == '')) {
							this.showTips = true;
							this.msg = '晚市餐时设置必须填写完整';
							setTimeout(()=>{
								this.showTips = false;
							},1500);
							return err = true;
						}
					}
					if(item === Dinnertime.NIGHT) {
						if((Util.isEmpty(this.value10) || this.value10 == '') || (Util.isEmpty(this.value11) || this.value11 == '')) {
							this.showTips = true;
							this.msg = '夜宵餐时设置必须填写完整';
							setTimeout(()=>{
								this.showTips = false;
							},1500);
							return err = true;
						}
					}

				});
				if(err) {
					return;
				}
//				if(!this.result) {
//					alert('营业开始时间不能大于结束时间！');
//					return;
//				}
//				if(!this.moresults) {
//					alert('餐时(早市)开始时间不能大于结束时间！');
//					return;
//				}
//				if(!this.noresults) {
//					alert('餐时(午市)开始时间不能大于结束时间！');
//					return;
//				}
//				if(!this.evresults) {
//					alert('餐时(晚市)开始时间不能大于结束时间！');
//					return;
//				}
				let {
					value2,
					value3,
					value4,
					value5,
					value6,
					value7,
					value8,
					value9,
					value10,
					value11
				} = this,
				opening_time = [],
					start = moment(this.value2).format("HH:mm:ss"),
					end = moment(this.value3).format("HH:mm:ss"),
					open_time = [] ;
					open_time.push(start,end);
				this.type.forEach(item => {
					if(item == Dinnertime.MORN) {
						opening_time.push({
							'type': Dinnertime.MORN,
							'from': { ...this.formatTime(value4)
							},
							'to': { ...this.formatTime(value5)
							}
						})
					}
					if(item == Dinnertime.NOON) {
						opening_time.push({
							'type': Dinnertime.NOON,
							'from': { ...this.formatTime(value6)
							},
							'to': { ...this.formatTime(value7)
							}
						})
					}
					if(item == Dinnertime.EVEN) {
						opening_time.push({
							'type': Dinnertime.EVEN,
							'from': { ...this.formatTime(value8)
							},
							'to': { ...this.formatTime(value9)
							}
						})
					}
					if(item == Dinnertime.NIGHT) {
						opening_time.push({
							'type': Dinnertime.NIGHT,
							'from': { ...this.formatTime(value10)
							},
							'to': { ...this.formatTime(value11)
							}
						})
					}
				})
				if(this.show === Invoce.YES && this.showinvs !== Invoce.NO && this.inType.indexOf(this.showinvs) === -1) {
					this.inType.push(this.showinvs)
				}
				if(this.dinner.length === 0){
					this.dinner.push(Saleway.SHOPEAT, Saleway.PACKAGING, Saleway.TAEOUT, Saleway.PICKUP);
				}
				if(this.timePay.length === 0){
					this.timePay.push(PayTime.BEFOREPAY, PayTime.AFTERPAY);
				}
				if(this.pay.length === 0){
					this.pay.push(Payway.CASHPAYMENT, Payway.CARDPAYMENT, Payway.ALIPAY, Payway.WECHATPAY, Payway.IOUPAY);
				}
				this.invoies.invoice_type = this.inType;
				let shop_id = Public.getShopid();
				let data = {
					shopinfo_save: 1,
					shop_id: shop_id,
					suspend: this.startsystem,
					is_invoice_vat: this.invoies,
					shop_pay_way: this.pay,
					pay_time: this.timePay,
					sale_way: this.dinner,
					shop_label: this.shoplabel,
					open_time,
					invoice_remark: this.remark,
					opening_time,
					img_list: this.shoppic
				};
				shopSeting(data, this.alts);
			},
			//保存数据成功的回调
			alts(resp) {
				if(resp.ret === 0) {
					this.showTips = true;
					this.msg = '成功保存店铺设置';
					setTimeout(()=>{
						this.$router.go(-1);
					},1500);
					
				} else {
					this.showTips = true;
					this.msg = '店铺设置保存失败';
					setTimeout(()=>{
						this.showTips = false;
					},1500);
					return;
				}
			},
			/*餐时设置*/
			setTimes(arr, y, m, d) {
				let type = [];
				arr.forEach(item => {
					if(item.type == Dinnertime.MORN) {
						type.push(Dinnertime.MORN)
						this.value4 = new Date(y, m, d, item.from.hh, item.from.mm, item.from.ss);
						this.value5 = new Date(y, m, d, item.to.hh, item.to.mm, item.to.ss);
					} else if(item.type == Dinnertime.NOON) {
						type.push(Dinnertime.NOON)
						this.value6 = new Date(y, m, d, item.from.hh, item.from.mm, item.from.ss);
						this.value7 = new Date(y, m, d, item.to.hh, item.to.mm, item.to.ss);
					} else if(item.type == Dinnertime.EVEN) {
						type.push(Dinnertime.EVEN)
						this.value8 = new Date(y, m, d, item.from.hh, item.from.mm, item.from.ss);
						this.value9 = new Date(y, m, d, item.to.hh, item.to.mm, item.to.ss);
					} else if(item.type == Dinnertime.NIGHT) {
						type.push(Dinnertime.NIGHT)
						this.value10 = new Date(y, m, d, item.from.hh, item.from.mm, item.from.ss);
						this.value11 = new Date(y, m, d, item.to.hh, item.to.mm, item.to.ss);
					}
				})
				this.type = type;
			},
			/*餐时设置*/
			//获取数据
			getData() {
				let _this = this;
				let t = new Date();
				let year = t.getFullYear();
				let mon = t.getMonth();
				let dats = t.getDate();
				getShopinfo({
					get_shopinfo_edit: 1
				}, function(resp) {
					if(resp) {
						_this.shopsystem = resp.data.shopinfo || {};
						if(!Util.isEmpty(_this.shopsystem.opening_time)) {
							_this.setTimes(_this.shopsystem.opening_time, year, mon, dats);
						}
						if(_this.shopsystem.opening_time.length === 0){
							_this.type = [Dinnertime.MORN, Dinnertime.NOON, Dinnertime.EVEN, Dinnertime.NIGHT];
							_this.value4 = new Date(2017, 10, 11, 6, 0, 0);
							_this.value5 = new Date(2017, 10, 11, 9, 59, 59);
							_this.value6 = new Date(2017, 10, 11, 10, 0, 0);
							_this.value7 = new Date(2017, 10, 11, 15, 59, 59);
							_this.value8 = new Date(2017, 10, 11, 16, 0, 0);
							_this.value9 = new Date(2017, 10, 11, 21, 59, 59);
							_this.value10 = new Date(2017, 10, 11, 22, 0, 0);
							_this.value11 = new Date(2017, 10, 12, 5, 59, 59);
						}
						_this.startsystem = _this.shopsystem.suspend || Startsystem.START;
						if(!Util.isEmpty(_this.shopsystem.is_invoice_vat)) {
							_this.invoies = _this.shopsystem.is_invoice_vat;
							_this.show = _this.invoies.is_invoice || Invoce.NO;
							_this.inType = _this.invoies.invoice_type || [];
						}
						if(_this.show === Invoce.NO){
							_this.showvos = Invoce.NO;
						}
						if(!Util.isEmpty(_this.inType)) {
							if(_this.inType.length >= 3) { //如果长度大于等于3 就把电子发票从数组删除
								_this.showinvs = _this.inType.pop();
							}
							if(_this.inType.length === 1 && _this.inType.indexOf(Invoce.ELECTRONICINVOICE) !== -1) { //如果长度等于1并且第一个下标的数字是3改变纸质发票的选中状态 纸票子类的显示与隐藏
								_this.showinvs = _this.inType.pop();
								_this.showinvos = Invoce.NO;
								_this.showvos = Invoce.NO;
							}
							if(_this.inType.length===2 && _this.inType.indexOf(Invoce.ELECTRONICINVOICE) !== -1){
								_this.showinvs = _this.inType.pop();
							}
							if(_this.inType.indexOf(Invoce.YES) !== -1 || _this.inType.indexOf(Invoce.PAPERINVOICE) !== -1) { //如果数组里面有1或2就改变纸质发票的选中状态
								_this.showinvos = Invoce.PAPERINVOICE;
							}
							if(_this.show === Invoce.NO) {
								_this.showinvos = Invoce.NO;
								_this.showvos = Invoce.NO;
							}
						}
						_this.pay = _this.shopsystem.shop_pay_way || [Payway.CASHPAYMENT, Payway.CARDPAYMENT, Payway.ALIPAY, Payway.WECHATPAY, Payway.IOUPAY];
						_this.timePay = _this.shopsystem.pay_time || [PayTime.BEFOREPAY, PayTime.AFTERPAY];
						_this.dinner = _this.shopsystem.sale_way || [Saleway.SHOPEAT, Saleway.PACKAGING, Saleway.TAEOUT, Saleway.PICKUP];
						_this.shoplabel = _this.shopsystem.shop_label || [];
						_this.remark = _this.shopsystem.invoice_remark || '';
						if(!Util.isEmpty(_this.shopsystem.img_list)) {
							_this.shoppic = _this.shopsystem.img_list;
						}
						if(!Util.isEmpty(_this.shopsystem.open_time)) {
							let times = _this.shopsystem.open_time;
							if(times.length !== 0) {
								_this.value2 = new Date(year, mon, dats,...times[0].split(':'));
								_this.value3 = new Date(year, mon, dats,...times[1].split(':'));
							}
						}

					}

				});
			},

			//跳转回店铺信息页面
			goBack() {
				this.$router.go(-1);
			},
			//纸质发票
			showInvos() {
				if(this.showinvos === Invoce.NO) {
					this.showinvos = Invoce.PAPERINVOICE;
					this.showvos = Invoce.SHOWPAPERINVOICE;
					this.show = Invoce.YES;
				} else {
					this.showinvos = Invoce.NO;
					this.showvos = Invoce.NO;
				}
			},
			//电子发票
			addCls() {
				if(this.showinvs === Invoce.NO) {
					this.showinvs = Invoce.ELECTRONICINVOICE;
					this.show = Invoce.YES;
				} else {
					this.showinvs = Invoce.NO;

				}
			},
			/*添加餐厅标签弹窗*/
			addlabel() {
				this.dialogVisible = true;
			},
			//关闭弹窗
			hideDialog() {
				
				this.dialogVisible = false;
			},
			//获取店铺标签
			getLabel(select) {
				this.shoplabel = select;
			},
			/*添加餐厅标签弹窗*/
			
			//点击出现大图
			clickImg(index) {
				this.showImg = true;
				// 获取当前图片地址
				this.imgSrc = this.shoppic[index];
			},
			//点击关闭大图
			viewImg() {
				this.showImg = false;
			},
			/*判断开始时间不能大于结束时间*/
			checkData(startTime, endTime) {
				if(startTime === "" || endTime === "") {
					return true;
				}

				let start = parseInt(moment(startTime).format("X"));
				let end = parseInt(moment(endTime).format("X"));

				if(start > end) {
					this.showTips = true;
					this.msg = '开始时间不能大于结束时间';
					setTimeout(()=>{
						this.showTips = false;
					},1500);
					return false;
				} else {
					return true;
				}
			},
			bustimeStartChange() {
				let results = this.checkData(this.value2, this.value3);
				this.result = results;
			},
			bustimeEndChange() {
				let results = this.checkData(this.value2, this.value3);
				this.result = results;
			},
			dinnemoStartChange() {
				let moresult = this.checkData(this.value4, this.value5);
				this.moresults = moresult;
			},
			dinnemoEndChange() {
				let moresult = this.checkData(this.value4, this.value5);
				this.moresults = moresult;
			},
			dinnenoStartChange() {
				let noresult = this.checkData(this.value6, this.value7);
				this.noresults = noresult;
			},
			dinnenoEndChange() {
				let noresult = this.checkData(this.value6, this.value7);
				this.noresults = noresult;
			},
			dinneevStartChange() {
				if(this.value8 !== '' && !Util.isEmpty(this.value8) || this.value9 !== '' && !Util.isEmpty(this.value9) ){
					let evresult = this.checkData(this.value8, this.value9);
					this.evresults = evresult;
				}
			},
			dinneevEndChange() {
				if(this.value8 !== '' && !Util.isEmpty(this.value8) || this.value9 !== '' && !Util.isEmpty(this.value9) ){
					let evresult = this.checkData(this.value8, this.value9);
					this.evresults = evresult;
				}
			}
			/*判断开始时间不能大于结束时间*/
		},
		watch: {
			show: function(newVal) {
				this.invoies.is_invoice = newVal;
			}
		}

	};
</script>

<style lang="scss" scoped>
	@import 'src/styles/mixin.scss';
	.warp {
		position: relative;
		.tips {
			position: fixed;
			z-index:10;
			@include wh(360px, 100px);
			background: #000;
			opacity: 0.8;
			left: 50%;
			top: 50%;
			margin-left: -180px;
			margin-top: -50px;
			text-align: center;
			border-radius: 2px;
			.info {
				display: inline-block;
				@include fc(14px, #fff);
				padding-top: 40px;
				letter-spacing: 0.5;
			}
		}
		button {
			cursor: pointer;
			outline: none;
		}
		.active {
			background: #4877E7 !important;
			color: #fff !important;
			border-radius: 2px !important;
		}
		.selectd {
			color: #4877E7;
			background-color: #fff;
			border-color: #4877E7 !important;
			box-shadow: none;
			position: relative;
			&:after {
				position: absolute;
				right: 0;
				top: 0;
				content: '';
				@include wh(12px, 12px);
				background: url(../../img/select.png)no-repeat;
			}
		}
		.shopinfo {
			position: relative;
			@include wh(100px, 100px);
			background: #e1e1e1;
			margin-right: 30px;
			img {
				width: 100%;
				height: 100%;
				display: block;
				transition: opacity .3s;
			}
		}
		.shopinfo:hover .bg {
			position: absolute;
			left: 0;
			top: 0;
			@include wh(100px, 100px);
			background: rgba(0, 0, 0, .5);
			cursor: pointer;
		}
		.label {
			display: inline-block;
			@include wh(80px, 30px);
			border: 1px solid #F2F2F2;
			background: #F2F2F2;
			border-radius: 2px;
			@include fc(14px, #666);
			text-align: center;
			line-height: 30px;
			margin-right: 10px;
		}
		.add {
			display: inline-block;
			@include wh(80px, 30px);
			border: 1px solid #4877E7;
			@include fc(14px,#4877E7);
			line-height: 30px;
			text-align: center;
			cursor: pointer;
		}
		.nvs {
			background: #EFF3FA;
			height: 26px;
		}
		background:#fff;
		.title {
			text-indent: 14px;
			@include fc($sizes, $grey);
			height: 40px;
			line-height: 40px;
			background: #F6F8FC;
		}
		.shopSeting {
			margin: 40px 0 0 162px;
			.injection {
				@include fc(12px, #9b9b9b);
			}
			.start {
				@include wh(80px, 30px);
				line-height: 30px;
				@include fc(14px, #666);
				border: 1px solid #d8d8d8;
				background: #fff;
				&:first-child {
					margin-right: 10px;
				}
			}
			.line {
				float: left;
				display: inline-block;
				height: 30px;
				width: 1px;
				background: #d8d8d8;
				margin: 4px 20px 0 20px;
			}
			.paperInvoice,
			.electronicInvoice {
				@include wh(100px, 30px);
				background: #fff;
				border: 1px solid #d8d8d8;
			}
			.paperInvoice {
				margin-right: 6px;
			}
			.shopPic {
				@include wh(100px, 100px);
				background: #e1e1e1;
			}
			.busPic {
				@include wh(100px, 100px);
				background: #e1e1e1;
				img {
					width: 100%;
					height: 100%;
					display: block;
				}
			}
			.text-warp {
				position: absolute;
				left: 0px;
				top: 60px;
				@include fc(12px, #9b9b9b);
				margin-top: 40px;
			}
			.picInfo {
				width: 54%;
				margin-left: 20px;
			}
			.noInvoice {
				@include wh(100px, 30px);
				line-height: 30px;
				@include fc(14px, #666);
				background: #fff;
				border: 1px solid #d8d8d8;
				margin-top: 4px;
			}
		}
		.bottom {
			width: 100%;
			text-align: center;
			padding-bottom:20px;
			padding-top: 40px;
			margin-left: 280px;
			.blue {
				@include fc(16px, #ffffff);
				background: #4877E7;
				@include wh(160px, 40px);
				line-height: 40px;
				border-radius: 6px;
			}
			.default {
				@include fc(14px, #4877E7);
				background: #ffffff;
				@include wh(160px, 40px);
				line-height: 40px;
				border-radius: 6px;
				border: 1px solid #4877E7;
				margin-left: 50px;
			}
		}
	}
</style>

<style lang="scss">
	@import 'src/styles/mixin.scss';
	#shopSeting {
		#top {
			margin-bottom: 5px;
		}
		.dateSet .el-input__inner {
			@include wh(120px, 34px);
			margin: 5px;
		}
		.el-input__inner {
			border-radius: 0;
		}
		.businessTime .el-input__inner {
			@include wh(120px, 34px);
			margin-right: 10px;
		}
		.el-checkbox__label {
			padding-left: 9px;
		}
		.el-textarea__inner {
			@include wh(400px, 100px);
			resize: none;
			border-radius: 0;
			border: 1px solid #D9D9D9;
		}
		.picInfo .el-button {
			width: 60px;
			height: 30px;
			background: #ffffff;
			font-size: 14px;
			color: #4877E7;
			line-height: 30px;
			border-radius: 0;
			padding: 0;
			border-color: #4877E7;
		}
		.el-button {
			border: 1px solid #D8D8D8;
			border-radius: 2px;
			padding: 0;
		}
		.main .el-checkbox-button__inner {
			padding: 0;
			@include wh(80px, 30px);
			line-height: 30px;
			color: #666;
			border: 1px solid #d8d8d8;
			border-radius: 2px;
		}
		.el-checkbox-button.is-checked .el-checkbox-button__inner {
			color: #4877E7;
			background-color: #fff;
			border-color: #4877E7;
			box-shadow: none;
			position: relative;
			&:after {
				position: absolute;
				right: 0;
				content: '';
				@include wh(12px, 12px);
				background: url(../../img/select.png)no-repeat;
			}
		}
		.paperTickets .el-checkbox-button__inner {
			@include wh(160px, 30px);
			padding: 0;
			line-height: 30px;
			color: #666;
			border: 1px solid #d8d8d8;
			border-radius: 2px;
		}
		.invoice .invoce {
			@include wh(160px, 30px);
			padding: 0;
			line-height: 30px;
			color: #666;
			border: 1px solid #d8d8d8;
			border-radius: 2px;
			margin-right: 10px;
		}
		.nomain .el-checkbox-button__inner {
			@include wh(100px, 30px);
			padding: 0;
			line-height: 30px;
			color: #666;
			border: 1px solid #d8d8d8;
			border-radius: 2px;
		}
		.el-breadcrumb__item__inner,
		.el-breadcrumb__item__inner a {
			color: #333;
			font-size: 12px;
		}
		.el-breadcrumb__separator {
			margin: 0 5px;
			color: #333;
		}
		.el-breadcrumb__item:last-child .el-breadcrumb__item__inner,
		.el-breadcrumb__item:last-child .el-breadcrumb__item__inner a,
		.el-breadcrumb__item:last-child .el-breadcrumb__item__inner a:hover,
		.el-breadcrumb__item:last-child .el-breadcrumb__item__inner:hover {
			color: #4877E7;
		}
		.el-date-editor.el-input {
			width: 120px;
			margin-right: 8px;
		}
		.el-checkbox-button,
