			border-radius: 0;
		}
		.dateSet {
			min-width: 760px;
			max-width: 800px;
		}
		.el-icon-delete2 {
			position: relative;
			opacity: 0;
			&:before {
				position: absolute;
				left: 60px;
				top: -80px;
