				color: #fff;
			}
		}
		.el-icon-view {
			position: relative;
			opacity: 0;
			&:before {
				position: absolute;
				left: 20px;
				top: -80px;
				font-size: 20px;
				color: #fff;
				cursor: pointer;
			}
		}
		.img-view .img img {
			width: 600px;
			height: 400px;
			display: block;
			position: fixed;
			left: 50%;
			top: 50%;
			margin-left: -300px;
			margin-top: -200px;
			z-index: 1001;
		}
		.shopinfo:hover .el-icon-delete2 {
			opacity: 1;
		}
		.shopinfo:hover .el-icon-view {
			opacity: 1;
		}
	}
</style>


